import React from "react";
import Header from "./components/Header";
import Main from "./components/Main";
import Footer from "./components/Footer";
import './css/base.css';

const App = () => {
    return (
       <React.Fragment>
           <div className="wrapper">
                <Header />
                <Main />
            </div>
                <Footer />
       </React.Fragment>
    );
};

export default App;